.wrapper {
  width: 100%;
  height: 100%;
  background: #1C1C21;
  position: fixed;
}

.container {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  flex-direction: column;
}

.link {
  text-decoration: none;
  color: #4C4CFF;
  transition: cubic-bezier(0.215, 0.610, 0.355, 1);
}

.link:hover {
  opacity: .7;
}
