.ingredient {
  width: 272px;
  height: 208px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0 0 0 16px;
  column-gap: 5px;
}

.image {
  display: block;
  width: 240px;
  height: 120px;
  margin: 0;
  margin-top: 23px;
  transition: all .1s ease-in;;
  cursor: grab;
}

.image:hover {
  opacity: .6;
	filter: drop-shadow(0px 4px 32px rgba(51, 51, 255, 0.5));
}

.price {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: 5px;
}

.counter {
  position: absolute;
  top: 24px;
  left: 289px;
}